*{
    margin:0;
    padding:0;
}

body{
    font-family: sans-serif;
}
h2{
    font-size: 20px;
    margin: 15px 0;
    color: grey;
}

p{
    margin: 0;
    font-size: 20px;
}

.main{
    display: flex;
    width: 100vw;
    height: 100vh;
    justify-content: center;
    align-items: center;
    background-color: #39b5ab;
}

#quote-box{
    width: 450px;
    padding: 40px;
    border-radius: 10px;
    background-color: #ffffff;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
}

.actions{
    display: flex;
    align-items: center;
}

.main .button, #tweet-quote{
    background-color: #9139b5;
    border-color: #9139b5;
    display: inline-block;
    border-radius: 5px;
    padding: 5px 10px;
    cursor: pointer;
    font-size: 20px;
    color: #ffffff;
}

.main .button:hover{
    background-color: #b35dd4;
}

#tweet-quote{
    margin-left: 15px;
    padding: 7px 10px;
    text-decoration: none;
}

#tweet-quote:hover{
    background-color: #b35dd4;
}